import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {Avatar, Button, Card, Col, Input, Modal, notification, Row, Select, Tag} from "antd";
import {EditOutlined, EllipsisOutlined, SettingOutlined} from '@ant-design/icons';
import axios from 'axios';

import "./style.css"
import karaoke from "./../../karaoke.json"
import {Option} from "antd/es/mentions";

const {Meta} = Card;

const Record = (props) => (
    <tr>
        <td>{props.record.name}</td>
        <td>{props.record.position}</td>
        <td>{props.record.level}</td>
        <td>
            <Link className="btn btn-link" to={`/edit/${props.record._id}`}>Edit</Link> |
            <button className="btn btn-link"
                    onClick={() => {
                        props.deleteRecord(props.record._id);
                    }}
            >
                Delete
            </button>
        </td>

    </tr>
);

export default function Karaoke() {
    const [records, setRecords] = useState([]);
    const [users, setUsers] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [inputBgk, setInputBgk] = useState();
    const [adminPass, setAdminPass] = useState();
    let currentBgk = localStorage.getItem("bgk2");

    const showModal = () => {
        setIsModalOpen(true);
    };

    const openNotification = () => {
        notification.open({
            message: 'Thông báo',
            description:
                'Tài khoản đang được đăng nhập thiết bị khác, vui lòng liên hệ quản trị.'
        });
    };
    const handleOk = () => {
        if(inputBgk !== "Admin" && inputBgk !== "MC" ) {
            let user = users.filter(u=>u.name === inputBgk);
            if (!user[0].status) {
                setIsModalOpen(false);
                localStorage.setItem("bgk2", inputBgk);
                handleStatus(user)
            } else {
                openNotification()
            }
        } else if (inputBgk === "MC"){
            setIsModalOpen(false);
            localStorage.setItem("bgk2", inputBgk);
        } else if (adminPass === "1234"){
            setIsModalOpen(false);
            localStorage.setItem("bgk2", inputBgk);
        }
    };

    async function handleStatus(record) {
        await fetch(`https://karaserver.onrender.com/users/update/${record[0]._id}`, {
            method: "POST",
            body: JSON.stringify({status: true}),
            headers: {
                'Content-Type': 'application/json'
            },
        }).then(() => {
            getUsers()
        })
    }
    const handleCancel = () => {
        setIsModalOpen(false);
    };
    const handleSort = () => {
        let result = records.sort((a, b) => {
            let nameA = 0
            a.scores.forEach(e=>{
                nameA = nameA + e.total;
            });
            let nameB = 0
            b.scores.forEach(e=>{
                nameB = nameB + e.total;
            });
            if (nameA < nameB) {
                return 1;
            }
            if (nameA > nameB) {
                return -1;
            }
            return 0;
        });
        setRecords(result)
    };
    // This method fetches the records from the database.
    useEffect(() => {
        if (!currentBgk) {
            setIsModalOpen(true);
        } else if(currentBgk === "MC") {
            setIsModalOpen(true);
        }
    }, []);
    async function getRecords() {
        const response = await fetch(`https://karaserver.onrender.com/karaoke`);
        const records = await response.json();
        setRecords(records);
    }
    async function getUsers() {
        const response = await fetch(`https://karaserver.onrender.com/users`);
        const records = await response.json();
        setUsers(records);
    }
    useEffect(() => {
        getRecords();
        getUsers()
        return;
    }, [records.length]);

    // This method will delete a record
    async function deleteRecord(id) {
        await fetch(`https://karaserver.onrender.com/karaoke/${id}`, {
            method: "DELETE"
        });

        const newRecords = records.filter((el) => el._id !== id);
        setRecords(newRecords);
    }
    function recordList() {
        return records.map((record) => {
            let score = 0;
            let did = 0;
            if (record.scores && record.scores.length > 0) {
                if (currentBgk === "MC" || currentBgk === "Admin") {
                    record.scores.forEach(r => {
                        score = score + r.total;
                        if(r.total > 0) did = did + 1
                    })
                    if (did > 0) score = Math.round(score / did * 10) / 10
                } else {
                    let list = record.scores.filter(r => r.gk === currentBgk);
                    list && list.length > 0 && list.forEach(r => {
                        score = score + r.total;
                    })
                }
            }
            return (
                <Col span={8}>
                    <Link to={currentBgk !=="MC" ? `/karaoke/edit/${record._id}`:`/karaoke`}>
                        <Card title={record.name} bordered={false}
                              style={{
                                  height: "210px",
                                  textAlign: "center",
                                  marginBottom: "8px",
                                  borderRadius: "8px",
                                  boxShadow: "#0066aa80 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px"
                              }}>
                            <div style={{
                                heigth: "inherit",
                                backgroundImage: 'url("data:img/png;base64,")',
                                backgroundRepeat: "no-repeat",
                                opacity: 0.1,
                                backgroundSize: "cover",
                                height: "100px",
                                width: "100px",
                                position: "absolute"
                            }}/>
                            <Tag color="geekblue" style={{marginRight: "4px"}}>{record.sbd}</Tag>

                            <span style={{fontWeight: "bold", color: "#0066aa", fontSize: '2em',}}>{score}</span>
                            <span style={{fontSize: "0.5em"}}>điểm</span>
                            <br/>
                            <strong>{record.tietmuc}</strong>
                            <br/>
                            {record.hinhthuc}
                            <br/>
                        </Card>
                    </Link>
                </Col>
            );
        });
    }

    // This following section will display the table with the records of individuals.
    return (
        <div>
            <Row>
                <h3>Danh sách thí sinh</h3>&nbsp;
                {currentBgk === "Admin" && <Link to={"/karaoke/chitiet"}><Button>Chi tiết</Button></Link>}
                {/*<Button onClick={handleSort}>Sắp xếp</Button>*/}
            </Row>
            <div className="card-wrapper">
                <Modal title="Chọn tên ban giám khảo" open={isModalOpen} onOk={handleOk} closable={false}
                       maskClosable={false}
                       footer={[
                           <Button key="ok" onClick={handleOk}>
                               OK
                           </Button>]}
                >
                    {/*<Input placeholder="Ví dụ: PHT" onChange={e => {*/}
                    {/*    setInputBgk(e.target.value)*/}
                    {/*}}/>*/}
                    <Select style={{ width: "80%" }} allowClear onChange={e=>{setInputBgk(e)}}>
                        {users.map(u=>{
                            return(
                                <Option value={u.name}>{u.name}</Option>
                            )
                        })}
                        <Option value={"MC"}>MC</Option>
                        <Option value={"Admin"}>Admin</Option>

                    </Select>
                    <hr/>
                    {inputBgk === "Admin" && <Input placeholder="****" onChange={e => {
                        setAdminPass(e.target.value)
                    }}/>}
                </Modal>
                <Row gutter={8}>
                    {recordList()}
                </Row>
            </div>
        </div>
    );
}
